import axios from "axios";

export function group_access_ships() {
  return axios({
    method: "GET",
    url: `https://13.124.69.255:90/api/ship_access_group/GetGroup_List`,
    mode: "no-cors",
  });
}
export function group_access_ship_ships(queryString) {
  return axios({
    method: "GET",
    url: `https://13.124.69.255:90/api/ship_access_group/Select_Group?${queryString}`,
    mode: "no-cors",
  });
}

export function group_access_ship_Save_GroupPrivilige(body) {
  return axios({
    method: "POST",
    url: `https://13.124.69.255:90/api/ship_access_group/Save_GroupPrivilige`,
    data: body,
    mode: "no-cors",
  });
}
export function group_access_ship_GetShip_List() {
  return axios({
    method: "GET",
    url: `https://13.124.69.255:90/api/ship_access_group/GetShip_List`,
    mode: "no-cors",
  });
}

